section.top-ten {
    margin: 20px 0;
}

.top-ten .item {
    position: relative;
    padding-left: 80px;
}

.top-ten .item .num {
    position: absolute;
    width: 150px;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
}

@media screen and (max-width:415px) {
    .top-ten .item .num {
        width: 100px;
    }

    .top-ten .item {
        padding-left: 70px;
    }
}