.footer {
    margin: 50px 0;
    font-size: 14px;
    color: rgb(216, 216, 216);
}

.footer>.container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

.footer .info-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 20px;
}

.footer .info-wrapper .logo {
    width: 100px;
}

.footer .copy-right>span {
    display: block;
    margin: 10px 0;
}

.footer .btn-wrapper {
    display: flex;
    gap: 20px;
}

.footer .copy-right.mobile {
    display: none;
}

@media screen and (max-width:1024px) {
    .footer {
        margin-bottom: 130px;
    }

    .footer .container {
        display: block;
    }

    .footer .info-wrapper {
        align-items: center;
        margin-bottom: 50px;
    }

    .footer .info-wrapper .logo {
        width: 40vw;
    }

    .footer .info-wrapper .copy-right {
        display: none;
    }

    .footer .btn-wrapper {
        width: 100%;
        flex-wrap: wrap;
        gap: 0px;
        justify-content: space-evenly;
    }

    .footer .btn-wrapper a {
        margin: 10px 5px;
    }

    .footer .copy-right.mobile {
        display: block;
        text-align: center;
        margin-top: 50px;
        font-size: 18px;
    }
}

@media screen and (max-width:415px) {
    .footer .copy-right.mobile {
        font-size: 14px;
    }
}