.menu {
    display: none;
}

@media screen and (max-width:1024px) {
    .menu {
        width: 100%;
        display: flex;
        align-items: center;
        font-size: 12px;
        box-sizing: border-box;
        padding: 10px;
        background-color: black;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 999;
    }

    .menu ul {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 20px;
    }

    .menu li {
        width: 20%;
        text-align: center;
    }

    .menu li>img {
        max-width: 50px;
    }
}