.call-to-action {
    position: relative;
    width: 100%;
    padding-top: 40px;
}

.call-to-action .pic {
    width: 100%;
}

.call-to-action .content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

@media screen and (max-width:1024px) {

    .call-to-action h2 {
        font-size: 18px;
    }

    .call-to-action .pic img {
        width: 100vw;
        height: 80vw;
        object-fit: cover;
    }
}