.container>.category {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}

section.video-list {
    margin: 30px 0;
}

section.video-list .category .more {
    margin: 10px 0;
}

section.video-list .mySwiper .swiper-slide {
    position: relative;
}

section.video-list .mySwiper .swiper-slide .title {
    box-sizing: border-box;
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    word-break: break-all;
    background-color: rgba(0, 0, 0, 0.7);
    padding: 10px;
    top: 0;
    left: 0;
    z-index: 1;
    line-height: 24px;
    opacity: 0;
    transition: all .3s ease;
    cursor: pointer;
}

section.video-list .mySwiper .swiper-slide .title:hover {
    opacity: 1;
}

@media screen and (max-width:1024px) {
    section.video-list .mySwiper .swiper-slide .title {
        display: none;
    }
}