.nav {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    background-color: rgba(0, 0, 0, 0.575);
}

.nav .wrapper {
    display: flex;
    align-items: center;
    gap: 20px;
    padding: 10px 0;
}

.nav .logo {
    width: 100px;
}

.nav ul {
    display: flex;
    align-items: center;
    gap: 30px;
}

.nav ul+.logo {
    margin-right: auto;
}

.nav ul li a {
    transition: color .3s ease;
}

.nav ul li a:hover {
    color: rgb(148, 148, 148);
}

.nav .search {
    cursor: pointer;
}

.nav .search-bar {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 5px 10px;
}

.nav .search-bar input {
    background-color: transparent;
    outline: none;
    box-shadow: 0 0 0 0;
    color: white;
    border: none;
}

.nav .search-bar input:focus {
    outline: none;
}

.nav .search-bar input::placeholder {
    color: rgb(197, 197, 197);
}


@media screen and (max-width:1024px) {
    .nav {
        display: none;
    }
}